import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import {
  CircularProgress,
  Container,
  createStyles,
  Theme,
  Typography,
  WithStyles,
  withStyles,
} from "@material-ui/core";
import { pathFinder } from "../../_helpers/constants";
import { requestPost } from "../../_actions/post.actions";
import { AppActions, AppState } from "../../_types";
import { IPost } from "../../_types/post.types";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      width: "99vw",
      minHeight: "calc(100vh - 128px)",
      [theme.breakpoints.down("xs")]: {
        minHeight: "calc(100vh - 112px)",
        padding: theme.spacing(1),
      },
      padding: theme.spacing(2),
      display: "flex",
      flexDirection: "column",
    },
    autoMargin: {
      margin: "auto",
    },
    bank: {
      textAlign: "center",
      width: "100%",
      padding: 10,
      backgroundColor: theme.palette.background.paper,
      border: `1px solid ${theme.palette.secondary.main}`,
      "& > h1": {
        padding: 10,
        margin: 10,
        fontWeight: 900,
        backgroundColor: theme.palette.secondary.main,
      },
      "& > h2": {
        padding: 10,
        margin: 10,
        fontWeight: 800,
      },
      "& > h3": {
        padding: 7,
        margin: 7,
        fontWeight: 800,
      },
      "& > img": {
        maxWidth: "100%",
      },
      "& > * > img": {
        maxWidth: "100%",
      },
      "& > h4": {
        padding: 5,
        margin: 5,
        fontWeight: 800,
      },
      "& > figure": {
        margin: "auto",
        padding: 10,
        "& > figcaption": {
          display: "none",
        },
      },
      "& > * > strong": {
        fontWeight: 800,
      },
    },
  });

interface PostProps extends WithStyles<typeof useStyles> {
  data?: IPost;
  loading: boolean;
  error: string;
  pathname: string;
}

type Props = PostProps & LinkDispatchProps;

class Post extends Component<Props> {
  componentDidMount() {
    const path = pathFinder(this.props.pathname);
    if (path.post) this.props.requestPost(path.post);
  }

  render() {
    const { classes, data, loading, error } = this.props;
    return (
      <Container className={classes.root}>
        {loading ? (
          <CircularProgress color="secondary" className={classes.autoMargin} />
        ) : data ? (
          <div
            className={classes.bank}
            dangerouslySetInnerHTML={{ __html: data.content }}
          />
        ) : (
          <Typography component="div" className={classes.autoMargin}>
            {error}
          </Typography>
        )}
      </Container>
    );
  }
}

interface LinkDispatchProps {
  requestPost: (id: number) => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): PostProps => ({
  pathname: state.router.location.pathname,
  classes: ownProps.classes,
  ...state.single.post,
});

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestPost: bindActionCreators(requestPost, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(Post));
